const mongoose = require('mongoose')


const USER = new mongoose.Schema({
    username:{
        type:String,
        required: [true, "username required"]
    },
    password:{
        type: String,
        required:true
    },
    email:{
        type:String,
        required: true,
        unique: true,
    },
})
const userModel = mongoose.model('User', USER)
module.exports = userModel